* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.country-list  {
  list-style: none;
}

.country-list li {
  margin-top: 10px;
}

.country-list li p.countries_name {
  display: flex;
  align-items: center;
  font-size: 18;
  font-weight: 700;
}

p.country_name {
  display: flex;
  align-items: center;
  font-size: 24;
  font-weight: 700;
}

.country_flag {
  width: 50px;
}

.country-list span, .country-info span {
  margin-left: 20px;
}
